html,body{
  height: 100%;
}
* {
  box-sizing: border-box;
}

.hd{
  width: 45%;
  height: 60%;
}
.vid{
  width: 200px;
  height: 200px;
}
.txt{
  font-family: Arial, Helvetica, sans-serif ;
  font-weight: 700;
  color: #4A164B;
  letter-spacing: 2px;
}
.stl{
  font-family: Arial, Helvetica, sans-serif ;
  
  color: #4A164B;
}
.video{
  width: 65%;
}
.inform{
  width: 35%;
}
@media only screen and (max-width:755px){
  .inform{
    width: 100%;
  }
  .vid{
    width: 80px;
    height: 80px;
  }
  .video{
    width: 100%;
  }
  .hd{
    width: 50%;
  
  }
}